import React, { useContext, useState } from "react"
import Styles from "./Dashboard.module.css"
import { NavLink, useHistory } from "react-router-dom"
import { AuthContext } from "./../../context/AuthContext"
import LogOut from "./../../assets/img/logout.png"

const Dashboard = () => {
    const history = useHistory()
    const auth = useContext(AuthContext) 
    const [show, setShow] = useState(false)
    const [open, setOpen] = useState(false)

    const logoutHandler = event => {
        event.preventDefault()
        auth.logout()
        history.push("/")
    }

    const showModal = event => {
        event.preventDefault()
        setShow(true)
    }

    const logoutCancel = event => {
        event.preventDefault()
        setShow(false)
        history.push("/")
    }

    const menuOpen = event => {
        event.preventDefault()

        if (open === false){
            setOpen(true)
        } else {
            setOpen(false)
        }
    }

    const items = [
        {url: "/panel/plan", id: 1, text: "План ухода"},
        {url: "/panel/calendar", id: 2, text: "Календарь"},
        {url: "/panel/stats", id: 3, text: "Показатели"},
        {url: "/panel/info", id: 4, text: "Материалы"},
        {url: "/panel/chat", id: 5, text: "Чат"},
    ]

    const elems = items.map(({url, id, text}) => {
        return(
            <li key={ id } className={Styles.item}>
                <NavLink to={ url } className={`${Styles.link} ${open ? Styles.open : ""}`}>{ id }
                    <span className={`${Styles.span} ${open ? Styles.open : ""}`}>{ text }</span>
                </NavLink>
            </li>
        )
    })

    return(
        <nav className={`${Styles.navbar} ${open ? Styles.open : ""}`}>
            <div className={Styles.flexBlock}>
                <div className={Styles.title}>
                <NavLink to="/plan" className={`${Styles.logo} ${open ? Styles.open : ""}`}>teleOpeka</NavLink>
                    <a href="/" className={Styles.ham} onClick={menuOpen}>
                        <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                        <span className={Styles.line}></span>
                        <span className={`${Styles.line} ${open ? Styles.open : ""}`}></span>
                    </a>
                </div>
                <ul className={Styles.menu}>
                    { elems }
                    <li className={Styles.item}>
                        <a href="/" className={`${Styles.link} ${Styles.colored} ${open ? Styles.open : ""}`} onClick={showModal}>
                            <img className={Styles.icon} src={LogOut} alt="logout"/>
                        </a>
                    </li>
                </ul>
            </div>
            <div className={`${Styles.overlay} ${show ? Styles.active : !show}`} onClick={logoutCancel}></div>
            <div className={`${Styles.message} ${show ? Styles.active : !show}`}>
                <p className={Styles.text}>Are you sure, that you want to logout?</p>
                <a href="/" className={`${Styles.submit} ${Styles.left}`} onClick={logoutHandler}>Yes</a>
                <a href="/" className={`${Styles.submit} ${Styles.right}`} onClick={logoutCancel}>No</a>
            </div>
        </nav>
    )
}

export default Dashboard